import vuex from 'vuex'
import Vue from 'vue'
Vue.use(vuex)


const state = window.sessionStorage.getItem("state") != null ? JSON.parse(window.sessionStorage.getItem("state")) : {
    ArticleList: [{
        articleaccount: "介绍了Mysql的原理，学习mysql的良好书籍，不二之选，从根儿上理解 MySQL",
        articlecontent: null,
        articleimgurl: "http://kodo.yelingfa.top/lyblog/article2.jpg",
        articletitle: "Mysql是怎样运行的",
        category: "Mysql",
        commentSum: 0,
        createbyName: "小王",
        createtime: "2023-02-02 00:10:05",
        id: 2,
        istop: 1,
        review: 138,
    }],
    rankList: [{
        id: "",
        articletitle: "",
        review: ""
    }],
    articleDetail: {},
    total: 1,
    category: null,
    loginInfo: {
        email: "",
        headimg: "",
        phonenumber: null,
        token: "",
        username: ""

    },
    activeIndex:"/main"
}

const getters = {
    getArticleList(state) {
        return state.ArticleList
    },
    getRankList(state) {
        return state.rankList
    },
    getArticleDetail(state) {
        return state.articleDetail
    },
    getTotal(state) {
        return state.total
    },
    getCategory(state) {
        return state.category
    },
    getToken(state) {
        return state.loginInfo
    },
    getActiveIndex(state) {
        return state.activeIndex
    },

}
// 唯一存值的方法，同步的
const mutations = {
    updatedArticleList(state, ArticleList) {
        state.ArticleList = ArticleList
    },
    updatedRankList(state, rankList) {
        state.rankList = rankList
    },
    updatedArticleDetail(state, articleDetail) {
        state.articleDetail = articleDetail
    },
    updatedTotal(state, total) {
        state.total = total
    },
    updatedCategory(state, category) {
        state.category = category
    },
    updatedToken(state, loginInfo) {
        state.loginInfo = loginInfo
    },
    updatedActiveIndex(state, activeIndex) {
        state.activeIndex = activeIndex
    },
}

const actions = {
    asyncUpdateArticleList(context, ArticleList) {
        context.commit('updatedArticleList', ArticleList)
    },
    asyncUpdatedRankList(context, rankList) {
        context.commit('updatedRankList', rankList)
    },
    asyncUpdatedArticleDetail(context, articleDetail) {
        context.commit('updatedArticleDetail', articleDetail)
    },
    asyncUpdatedTotal(context, total) {
        context.commit('updatedTotal', total)
    },
    asyncUpdatedCategory(context, category) {
        context.commit('updatedCategory', category)
    },
    asyncUpdatedToken(context, loginInfo) {
        context.commit('updatedToken', loginInfo)
    },
    asyncUpdatedActiveIndex(context, activeIndex) {
        context.commit('updatedActiveIndex', activeIndex)
    }
}
const store = new vuex.Store({
    state,
    getters,
    mutations,
    actions
})
export default store