import request from "@/utils/request";

export function getArticlePage(params={}){
    return request({
        url:'/article/list',
        method:'get',
        params
    })
}
export function getHotArticle(){
    return request({
        url:"/article/hot",
        method:"get"
    })
}

export function getTimeArticle(){
    return request({
        url:"/article/timeArticle",
        method:"get"
    })
}

export function getArticleDetailById(id){
    return request({
        url:`/article/detail/${id}`,
        method:"get"
    })
}

export function getArticleListByCategory(data={}){
    return request({
        url:"/article/category",
        method:"post",
        data
    })
}

