import request from "@/utils/request";

export function login(data = {}) {
    return request({
        method: 'post',
        url: '/user/login',
        data
    })
}
export function logout() {
    return request({
        method: 'get',
        url: '/user/logout',
    })
}

export function sendEmail(params = {}) {
    return request({
        method: 'get',
        url: '/sendEmail',
        params
    })
}

export function register(data = {}) {
    return request({
        method: 'post',
        url: '/user/register',
        data
    })
}


export function forgetPassword(data = {}) {
    return request({
        method: 'post',
        url: '/user/forgetPassword',
        data
    })
}

export function checkUserName(params = {}) {
    return request({
        method: 'get',
        url: '/user/checkUserName',
        params
    })
}

export function checkEmail(params = {}) {
    return request({
        method: 'get',
        url: '/user/checkEmail',
        params
    })
}

export function updateNameAndPhone(data = {}) {
    return request({
        method: 'post',
        url: '/user/updateNameAndPhone',
        data
    })
}

export function uploadUserImage(formData) {
    return request({
        method: 'post',
        url: '/upload/uploadUserImage',
        headers: {

            Accept: "application/json",

            "Content-Type": "multipart/form-data"

        },

        data: formData
    })
}