import Vue from "vue";
import VueRouter from "vue-router";

Vue.use(VueRouter)
const router = new VueRouter({
    routes:[
        {
            path:"/",
            redirect:'/main'
        },
        {
            path:"/home",
            component:()=>import("../components/MainPage.vue"),
            children:[
                {
                    path:"/main",
                    component:()=>import("../components/MainRight.vue"),
                },
                {
                    path:'/articleDetail/:id',
                    component:()=>import("../components/article/detail/ArticleDetail.vue")
                },
                {
                    path:"/user/userInfo",
                    component:()=>import("../components/user/UserInfo.vue")
                }
            ]
        },
        {
            path:"/message",
            component:()=>import("../components/message/MessagePage.vue")
        },
        {
            path:"/login",
            component:()=>import("../components/user/LoginPage.vue")
        },
        {
            path:"/register",
            component:()=>import("../components/user/RegisterPage.vue")
        },
        {
            path:"/forget",
            component:()=>import("../components/user/ForgetPassword.vue")
        }
    ]
    
})
router.beforeEach((to, from, next) => {
    // console.log(document.getElementsByClassName('el-main'));
    document.body.scrollTop = 0;
    document.documentElement.scrollTop = 0;
 
    // 兼容IE
    window.scrollTo(0,0);
    next();
})

export default router